import axios from 'axios'

export default function getRequest(store) {
  const service = axios.create({
    timeout: 50000,
  })

  // 
  let isLoginTimeout = false

  service.interceptors.request.use(
    (config) => {
      // api  base_url
      // baseURLstorecreatestore
      config.baseURL = store.getters.urls.getBaseUrl()

      // json
      config.headers['Content-Type'] = 'application/json; charset=UTF-8'

      const token = store.getters.token
      if (token) {
        // token-- ['Authorization']key 
        config.headers['Authorization'] = token
      }

      return config
    },
    (error) => {
      console.log(error)
      store.getters.message({
        message: error,
        type: 'error',
        duration: 5 * 1000,
      })
      Promise.reject(error)
    }
  )

  service.interceptors.response.use(
    (response) => {
      // console.log(response)
      // http200
      const res = response.data
      if (res.code !== undefined && res.code !== null) {
        if (res.code >= 500) {
          store.getters.message({
            message: res.message,
            type: 'error',
            duration: 5 * 1000,
          })
          return Promise.reject('')
        } else if (res.code === 401) {
          handle401()
        } else if (res.code === 403) {
          handle403(res)
        } else {
          return response
        }
      } else {
        console.log('', res) // for debug
        store.getters.message({
          message: '!!!',
          type: 'error',
          duration: 5 * 1000,
        })
        return Promise.reject('')
      }
    },
    (error) => {
      if (error.message === 'Request failed with status code 401') {
        handle401()
      } else if (error.message === 'Request failed with status code 403') {
        handle403({ message: '' })
      } else {
        console.log('err', error, JSON.stringify(error)) // for debug
        store.getters.message({
          message: '',
          type: 'error',
          duration: 5 * 1000,
        })
      }

      return Promise.reject(error)
    }
  )

  function handle401() {
    // 
    if (!isLoginTimeout) {
      isLoginTimeout = true
    } else {
      console.log('')
      try {
        // 
        store.getters.messageBox.close()
      } catch (error) {}
    }

    store.getters.messageBox
      .confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
      .then(() => {
        isLoginTimeout = false
        store.getters.apis.logout().then(() => {
          store.dispatch('logout')
          isLoginTimeout = false
          location.reload() // vue-router bug
        })
      })
  }

  function handle403(res) {
    store.getters.message({
      message: res.message,
      type: 'warning',
      duration: 5 * 1000,
    })
    return Promise.reject('')
  }

  return service
}
