<template>
  <div>
    <div class="table-responsive">
      <table
        class="table table-bordered table-hover table-striped table-condensed"
      >
        <thead>
          <tr>
            <th colspan="4">
              
            </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td class="name">
              
            </td>
            <td class="value">
              <!-- @if (this.has((PermissionFlags)16)) -->

              <a style="cursor: pointer;" @click="restart"></a>

              &nbsp;&nbsp;&nbsp;&nbsp;{{ model.rawUrl }}
            </td>
            <td class="name">
              
            </td>
            <td class="value">
              {{ model.contentRootPath }}
            </td>
          </tr>
          <tr>
            <td class="name">
              
            </td>
            <td class="value">
              <span title="">{{ model.host }}</span
              >
              <span title="">{{ model.localHost }}</span>
              &nbsp;<span title="">{{ model.remoteHost }}</span>
            </td>
            <td class="name">
              
            </td>
            <td class="value">
              <span :title="model.commandLine">{{ model.processName }}</span>
            </td>
          </tr>
          <tr>
            <td class="name">
              
            </td>
            <td class="value">
              {{ model.curDomainFriendlyName }}
              <!-- <a href="@Url.action("Main", new{ id = "Assembly" })" target="_blank" title=""></a>
                    <a href="@Url.action("Main", new{ id = "ProcessModules" })" target="_blank" title=""></a>
                    <a href="@Url.action("Main", new{ id = "ServerVar" })" target="_blank" title=""></a> -->
            </td>
            <td class="name">
              .net 
            </td>
            <td class="value">
              {{ model.envVersion }} &nbsp;{{ model.frameworkName }}
            </td>
          </tr>
          <tr>
            <td class="name">
              
            </td>
            <td class="value" :title="model.guid">
              {{ model.oSName }} {{ model.oSVersion }}
            </td>
            <td class="name">
              
            </td>
            <td class="value" :title="model.uuid">
              <span v-if="model.product !== undefined"
                >{{ model.product }}</span
              >
              {{ model.userName + '/' + model.machineName }}
            </td>
          </tr>
          <tr>
            <td class="name">
              
            </td>
            <td class="value" :title="model.cpuID">
              {{ model.processor }}
              {{ model.processorCount }}
              {{ model.cpuRate }}
              <span v-if="model.temperature > 0"
                >{{ model.temperature }} </span
              >
            </td>
            <td class="name">
              
            </td>

            <td
              class="value"
              title=""
            >
              {{ model.dateTimeNow }}{{ model.uptime }}
            </td>
          </tr>
          <tr>
            <td class="name">
              
            </td>
            <td class="value">
              {{ model.availableMemory }}M / {{ model.memory }}M
              /: {{ model.workingSet64 }}M/{{
                model.privateMemorySize64
              }}M GC: {{ model.totalMemory }}M
              <a @click="memoryFree" title=""></a>
            </td>
            <td class="name">
              
            </td>
            <td class="value">
              <!-- @process.totalProcessorTime.totalSeconds.toString("N2")  @process.startTime.toLocalTime().toFullString() -->
            </td>
          </tr>
          <tr>
            <td class="name">
              Session
            </td>
            <td class="value">
              <!-- @httpContext.session.keys.count()  -->
              <a
                @click="main('Session')"
                target="_blank"
                title="Session"
                >Session</a
              >
              <!-- @gc -->
            </td>
            <td class="name">
              <!-- @{ var app = ApplicationManager.load();} -->
              
            </td>
            <td class="value">
              <!--  @app.startTime.toLocalTime().toFullString() -->
            </td>
          </tr>
        </tbody>
      </table>
      <table
        class="table table-bordered table-hover table-striped table-condensed"
      >
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <!-- @foreach (AssemblyX item in ViewBag.myAsms)
            {
                <tr>
                    <td>@item.name</td>
                    <td>@item.title</td>
                    <td>@item.fileVersion</td>
                    <td>@item.version</td>
                    <td>@(item.compile.year <= 2000 ? "" : item.compile.toFullString())</td>
                    <td>@item.description</td>
                </tr>
            } -->
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {
        rawUrl: 'RawUrl',
        contentRootPath: 'ContentRootPath',
        host: 'Host',
        localHost: 'LocalHost',
        remoteHost: 'RemoteHost',
        commandLine: 'CommandLine',
        processName: 'ProcessName',
        curDomainFriendlyName: 'FriendlyName',
        envVersion: 'EnvVersion',
        frameworkName: 'FrameworkName',
        guid: 'Guid',
        oSName: 'OSName',
        oSVersion: 'OSVersion',
        product: 'Product',
        userName: 'UserName',
        machineName: 'MachineName',
        uuid: 'UUID',
        processor: 'Processor',
        processorCount: 'ProcessorCount',
        cpuRate: 'CpuRate',
        temperature: 0,
        dateTimeNow: 'DateTimeNow',
        uptime: 'Uptime',
        availableMemory: 'AvailableMemory',
        memory: 'Memory',
        workingSet64: 'WorkingSet64',
        privateMemorySize64: 'PrivateMemorySize64',
        totalMemory: 'TotalMemory',
      },
    }
  },
  methods: {
    restart() {
      this.$alert(
        'ASP.Net Core<br/>',
        '',
        {
          confirmButtonText: 'confirm',
          callback: (action) => {
            console.log(action)
          },
        }
      )
    },
    memoryFree() {
      console.log('memoryFree')
    },
    main(id) {
      console.log(id)
    },
  },
}
</script>

<style></style>
